const {ccclass, property} = cc._decorator;

@ccclass
export default class NewClass extends cc.Component {
    start () {
        var manager = cc.director.getCollisionManager();
        manager.enabled = false;
        setTimeout(() => {
            manager.enabled = true;
        }, 3000)
    }
    onCollisionEnter(other, self) {
        console.log('1')
    }
    // update (dt) {}
}
